<template>
    <header class="header">
      <nav class="header__wrapper">
        <ul class="header__nav">
          <li class="header__item header__logo">
            <a href="" class="header__link">
              <img src="img/logo.png" alt="">
            </a>
          </li>
          <li class="header__item header__shopping-cart">
            <router-link to="/shopping-cart" class="header__link"  tag="a"  :exact="true" v-if="numberOfGoods > 0">
              <div class="header__products">
                {{ numberOfGoods }}
              </div>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-shopping-cart">
                <circle cx="9" cy="21" r="1"></circle>
                <circle cx="20" cy="21" r="1"></circle>
                <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
              </svg>
            </router-link>

            <a class="header__link"  @click.prevent v-if="numberOfGoods < 1">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-shopping-cart">
                <circle cx="9" cy="21" r="1"></circle>
                <circle cx="20" cy="21" r="1"></circle>
                <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path>
              </svg>
            </a>
          </li>
        </ul>
      </nav>
    </header>
</template>

<script>
  import store from '../../store.js';

    export default {
      name: "HeaderComponent",
      // props: [ 'soppingCaseCounter' ],
      data() {
         return {
           numberOfGoods: 0,
         }
      },
      methods: {
        showShopingCart (event) {
          console.log( event );
        },
      },
      // watch: {
      //   funny() {
      //     this.numberOfGoods++;
      //     console.log('funny')
      //   }
      // },
      mounted() {
        this.numberOfGoods = store.getStoreLength();
      }
    }
</script>

<style lang="scss" scoped>
  $paddingNavVertical: .75%;

  .header {
    position: fixed;
    z-index: 99;
    width: 100%;
    // background-color: rgb(32, 31, 31);
    // background-color: red;
    // background: transparent;
    background-color: rgba(0,0,0,.1);
    &__wrapper {
       width: 100% - $paddingNavVertical * 5;
       margin: 0 auto;
    }
    &__nav {
      margin: 0;
      padding-left: 0;
      padding-top: $paddingNavVertical;
      padding-bottom: $paddingNavVertical;
      list-style: none;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    &__link {
      /*position: relative;*/
    }
    &__logo {
      img {
        height: 2.5em;
        width: auto;
      }
    }
    &__shopping-cart {
      a {
        color: #fff;
      }
      svg {
        height: 2em;
        width: auto;
      }
    }
    &__products {
      color: #fff;
      background-color: #b10000;
      width: 25px;
      height: 25px;
      z-index: 8;
      text-decoration: none;
      border-radius: 50%;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: "Oswald - Regular Italic";
      font-family: Hardcore;
      font-size: 1rem;
      font-weight: 400;
      right: 1%;
      top: 5%;
    }
  }
</style>



