<template>
    <article class="ad--unit">
      <div class="ad--unit__wrapper">
        <div class="ad--unit__col ad--unit__col--6">
          <img src="img/phone3.png" alt="">
        </div>
        <div class="ad--unit__col ad--unit__col--6">
          <div class="ad--unit__col--wrapper">
            <h3 class="ad--unit__title">
              clear style in your pocket.
            </h3>
            <h5 class="ad--unit__text">
              See the new APPLE IPHONE tempered safety glass.
            </h5>

            <a class="ad--unit__btn" @click.prevent="scrollToUp">Заказать</a>
          </div>
        </div>
      </div>
    </article>
</template>

<script>
    export default {
        name: "AdUnit",
        methods: {
          scrollToUp() {
            window.scrollTo(0, 0);
          }
        }
    }
</script>

<style scoped lang="scss">
  .ad--unit {
    color: #000;
    text-transform: uppercase;
    font-weight: bold;
    font-family: "Oswald - Regular Italic";
    @media screen and (max-width: 1550px) {
      font-size: 80%;
    }
    @media screen and (max-width: 1024px) {
      font-size: 70%;
    }
    @media screen and (max-width: 767px) {
      font-size: 50%;
    }
    &__wrapper {
       display: flex;
    }
    &__col {
      height: auto;
      display: flex;
      width: 50%;
      &:first-child {
        @media screen and (max-width: 767px) {
          display: none;
        }
      }
      &:last-child {
        background-color: #fff;
        padding: 1rem;
        @media screen and (max-width: 767px) {
          width: 100%;
          padding: 0;
        }
      }
      &--wrapper {
        border: 3px solid #000;
        width: 100%;
        padding: 5% 15%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;
        @media screen and (max-width: 767px) {
          padding: 3% 6%;
        }
      }
      img {
        width: 100%;
      }
    }
    &__title {
      font-family: "Oswald - Demi";
      font-size: 5.5em;
      font-style: italic;
      letter-spacing: 0.72px;
      margin: 2rem 0;
      @media screen and (max-width: 1550px) {
        font-size: 4.5em;
      }
    }
    &__text {
      margin: 1rem 0;
      font-size: 2.5em;
      letter-spacing: 0.9px;
    }
    &__btn {
      text-align: center;
      text-decoration: none;
      text-shadow: 4px 3px 9px rgba(0, 0, 0, 0.42);
      padding: .75rem 2.25rem;
      color: #ffffff;
      font-size: 49px;
      line-height: 74.05px;
      letter-spacing: 1px;
      border: none;
      border-radius: 10px;
      outline: none;
      cursor: pointer;
      background: linear-gradient(to right, #1371de 0%,#01388b 60%, #01388b 100%);
      box-shadow: 4px 3px 15px 1px rgba(0, 0, 0, 0.4),
                  inset 0 0 22px 2px rgba(41, 107, 216, 0.49);
      transition: all 1s;
      position: absolute;
      right: 15%;
      bottom: 5%;
      @media screen and (max-width: 767px) {
        position: static;
      }
      &:hover {
        background: linear-gradient(to right, #1371de 0%, #1371de 100%);
      }
      &:active {
        right: 14.75%;
        bottom: 4.75%;
        box-shadow: none;
      }
    }
  }
</style>



